#data__me__containter {
	height: 800px;
	width: 800px;
	background-color: blue;
	position: relative;
	overflow: hidden;
}

.data__me__element {
	transition: transform 0.1s;
}

.red__box {
	position: absolute;
	background-color: red;
	top: 50%;
	left: 50%;
	width: 50px;
	height: 50px;
}


.red__box:nth-child(2) {
	top: 80%;
	left: 30%;
}
